// @flow

import React, { Component } from 'react';
import {Container, Row, Col, Nav, Button, NavDropdown} from 'react-bootstrap';
import ScrollableAnchor, { configureAnchors } from 'react-scrollable-anchor';
import {Helmet} from 'react-helmet';
import ReactGA from 'react-ga';
import AsyncImg from '../widgets/AsyncImg';
import FTC1819 from '../../assets/home/FTC1819.jpg';
import Mission from '../../assets/home/Mission.jpg';
import CubScout from '../../assets/home/CubScout.jpg';
import GirlScout from '../../assets/home/GirlScout.jpg';
import IAState from '../../assets/home/IAState.jpg';
import STEMFest from '../../assets/home/STEMFest.jpg';
import CIJUG from '../../assets/home/CIJUG.jpg';
import NewMemberMtg from '../../assets/home/NewMemberMtg.jpg';
import Booth from '../../assets/home/Booth.jpg';
import KristenChild from '../../assets/home/KristenChild.jpg';
import '../stylesheet.css';

const nav =
<Nav variant="pills" className="flex-column" defaultActiveKey="#WhoWeAre">
  <Nav.Item><Nav.Link href="#WhoWeAre">Who We Are</Nav.Link></Nav.Item>
  <Nav.Item><Nav.Link href="#OurMission">Our Mission</Nav.Link></Nav.Item>
  <Nav.Item><Nav.Link href="#Impact">Impact</Nav.Link></Nav.Item>
</Nav>
;

const minWidth = 775;

export default class Home extends Component {
  constructor(props) {
    super(props);
    this.state = {
      width: window.innerWidth,
      selected: "Who We Are",
    };
    this.updateWindowWidth = this.updateWindowWidth.bind(this);
  }

  componentWillMount() {
    configureAnchors({offset: -90});
    window.addEventListener('resize', this.updateWindowWidth);
  }

  componentDidMount() {
    ReactGA.pageview(window.location.pathname);
    this.updateWindowWidth();
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.updateWindowWidth);
  }

  updateWindowWidth() {
    this.setState({width: window.innerWidth});
    if (this.state.width < minWidth) {
      configureAnchors({offset: -190});
    } else {
      configureAnchors({offset: -90});
    }
  }

  render() {
    return (
      <div id="home">
        <Helmet>
          <title>Home | Pathfinders Robotics</title>
          <meta name="description" content="Pathfinders Robotics is a parent-run 501(c)3 nonprofit organization dedicated to providing support for student-led FIRST robotics teams." />
        </Helmet>
        <Container fluid>
          <Row>
            <Col className="teamArticle standardFont">
              <ScrollableAnchor id="WhoWeAre">
                <div>
                  {this.state.width < minWidth ?
                    <div>
                      <div className="mobileBar">
                        <Nav variant="pills" className="flex-column frozen">
                          <NavDropdown title={this.state.selected}>
                            <NavDropdown.Item href="#WhoWeAre" onClick={() => this.setState({selected: "Who We Are"})}>Who We Are</NavDropdown.Item>
                            <NavDropdown.Item href="#OurMission" onClick={() => this.setState({selected: "Our Mission"})}>Our Mission</NavDropdown.Item>
                            <NavDropdown.Item href="#Impact" onClick={() => this.setState({selected: "Impact"})}>Impact</NavDropdown.Item>
                          </NavDropdown>
                        </Nav>
                      </div>
                      <br/>
                      <br/>
                    </div> : null
                  }
                  <h1>Who We Are</h1>
                  <AsyncImg src={FTC1819} alt="FTC Pathfinders 2018-2019" responsive="true" />
                  <p>
                    Originally founded in 2017 as a FIRST Tech Challenge Robotics Team, our team began to run a non profit organization as of March eighth of 2019. Pathfinders Robotics is a 501(c)3 nonprofit organization dedicated to providing support for FIRST robotics teams in our local area. We assist in the education of gracious professionalism, programming, and financial recommendations for other competing teams. We enjoy collaborating with both professionals and students alike to discover new and innovative solutions to solve challenges. In addition to the aforementioned collaborative exercises, our team enjoys sharing the knowledge we have accumulated over the years with our other FLL teams and FTC colleagues.
                  </p>
                </div>
              </ScrollableAnchor>
              <ScrollableAnchor id="OurMission">
                <div>
                  <h1>Our Mission</h1>
                  <AsyncImg src={Mission} alt="Pathfinders Robotics Mission" responsive="true" />
                  <p>
                    Pathfinders Robotics mission is to develop and educate the youth in grades five through twelve. We teach information pertaining to Science, Technology, Engineering, and Math (STEM). Through guidance and feedback we assist in the development of teams to compete in FIRST Tech Challenge, First Lego League, or other STEM-related events in Iowa, Regional, National and International competitions.  We prioritize the spread of STEM,  FIRST, and general robotics, usually directed at a younger audience. We embody gracious professionalism through our interactions with our competitors, financial partners, and professional associates. Gracious professionalism is important to our organization as a whole as it personifies meaningful interactions that create lasting bonds in the STEM field.
                  </p>
                  <p>
                    At Pathfinders Robotics, our mission is to support student-led FIRST
                    teams as best we can through the use of our student mentors, web
                    resources, gracious sponsors, and 501(c)3 nonprofit capabilities.
                  </p>
                </div>
              </ScrollableAnchor>
              <ScrollableAnchor id="Impact">
                <div>
                  <h1>Impact</h1>
                    <p>
                      Our community newspaper reached out to us for an interview regarding our involvement in FTC, our impact on the community, and what we do as an organization. We were featured on the front cover and given four pages in the article. You can find the article <a href="https://mywaukee.com/tech-teens/" target="_blank" rel="noopener">here</a>.
                    </p>
                  <br/>
                  <AsyncImg src={CubScout} alt="We were invited by a Cub Scout Troop to present about robotics to help them obtain a merit badge. We talked about FTC, FLL, FIRST, and how they all impact the community." responsive="true" />
                  <p>We were invited by a Cub Scout Troop to present about robotics to help them obtain a merit badge. We talked about FTC, FLL, FIRST, and how they all impact the community.</p>
                  <br/>
                  <br/>
                  <AsyncImg src={GirlScout} alt="Following our Cub Scout outreach, we presented robotics and FIRST to the NUMBER Girl Scout troop. They enjoyed learning about STEM and our activities which involved STEM." responsive="true" />
                  <p>Following our Cub Scout outreach, we presented robotics and FIRST to the NUMBER Girl Scout troop. They enjoyed learning about STEM and our activities which involved STEM.</p>
                  <br/>
                  <br/>
                  <AsyncImg src={IAState} alt="Iowa State University Professor of Aerospace Engineering R. Rajagopalan met with the Phoenix Voyagers and discussed their solution for fires in outer space with them. The Phoenix Voyagers and the FTC Pathfinders also toured Iowa States Virtual Reality Lab and met with several professionals there." responsive="true" />
                  <p>Iowa State University Professor of Aerospace Engineering R. Rajagopalan met with the Phoenix Voyagers and discussed their solution for fires in outer space with them. The Phoenix Voyagers and the FTC Pathfinders also toured Iowa States Virtual Reality Lab and met with several professionals there.</p>
                  <br/>
                  <br/>
                  <AsyncImg src={STEMFest} alt="The FTC Pathfinders set up a booth at Ankeny STEM Fest and taught children and their parents about FIRST, FTC, FLL. The children had a lot of fun driving FTC and FLL robots." responsive="true" />
                  <p>The FTC Pathfinders set up a booth at Ankeny STEM Fest and taught children and their parents about FIRST, FTC, FLL. The children had a lot of fun driving FTC and FLL robots.</p>
                  <br/>
                  <br/>
                  <AsyncImg src={CIJUG} alt="Our team was invited to present at CIJUG, we gave a presentation about our code and parts, demonstrated our robot, and explained our outreaches and volunteering." responsive="true" />
                  <p>Our team was invited to present at CIJUG, we gave a presentation about our code and parts, demonstrated our robot, and explained our outreaches and volunteering.</p>
                  <br/>
                  <br/>
                  <AsyncImg src={NewMemberMtg} alt="We hosted an informational meeting at one of our sponsors branch along with our sister team the Phoenix Voyagers, we gave a presentation about FIRST, we explained the different aspects of FLL and FTC and showed them our robots." responsive="true" />
                  <p>We hosted an informational meeting at one of our sponsors branch along with our sister team the Phoenix Voyagers, we gave a presentation about FIRST, we explained the different aspects of FLL and FTC and showed them our robots.</p>
                  <br/>
                  <br/>
                  <AsyncImg src={Booth} alt="During the summer, we set up a booth at the Hindu Temple Cultural Center where we demonstrated our robot and educated children about robotics." responsive="true" />
                  <br/>
                  <AsyncImg src={KristenChild} alt="During the summer, we set up a booth at the Hindu Temple Cultural Center where we demonstrated our robot and educated children about robotics." responsive="true" />
                  <p>During the summer, we set up a booth at the Hindu Temple Cultural Center where we demonstrated our robot and educated children about robotics.</p>
                  <br/>
                  <br/>
                </div>
              </ScrollableAnchor>
            </Col>
            <Col md="auto" className="sidenav">
              {this.state.width > minWidth ? nav : null}
              <Button variant="primary" href="#WhoWeAre" className="backToTop">Back to top</Button>
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}
