// @flow

import React, { Component } from 'react';
import { Form, DropdownButton, Dropdown, Button, ButtonGroup, Modal, Spinner } from 'react-bootstrap';
import {
  CardElement,
  PaymentRequestButtonElement,
  injectStripe
} from 'react-stripe-elements';
import {ValidationForm, TextInputGroup, Radio} from 'react-bootstrap4-form-validation';

import {Redirect} from 'react-router-dom';

import {prepErrorMsgObj} from './InformationForm';

import Info13497, { FTCPathfinders13497Tiers } from '../teams/13497/DonateInfo';
import Info7885, { FLLPathfinders7885Tiers } from '../teams/7885/DonateInfo';

import {getSecret, makePaymentRequest, paymentEmail} from '../../../api/payment';

import PathfindersRoboticsLogo from '../../../assets/Pathfinders-Logo.svg';
import AsyncImage from '../../widgets/AsyncImg';
import AnimatedCheckmark from '../../widgets/AnimatedCheckmark';

import ReactGA from 'react-ga';
import '../../stylesheet.css';

const amountRegex = /^[0-9]+(\.[0-9]{1,2})?$/;

const customAmount = "Custom Amount";

const creditDebitType = "Credit / Debit";
const paymentRequestType = "Apple Pay / Google Pay / Microsoft Pay";

const ftc13497 = "FTC Pathfinders 13497";
const fll7885 = "FLL Phoenix Voyagers 7885";

const updateInterval = 750;

class PaymentForm extends Component {
  componentDidMount() {
    ReactGA.pageview(window.location.pathname + '/PaymentForm');
  }

  constructor(props) {
    super(props);
    this.state = {
      amount: '',
      radioAmount: '',
      amountValid: false,
      team: /*'Recipient Team'*/ftc13497,
      //REMOVE THE LINE BELOW THIS LATER
      donateInfo: <Info13497/>,
      //MAKE THE VALUE BELOW THIS FALSE LATER
      teamValid: true,
      paymentType: 'Payment Type',
      paymentTypeValid: false,
      formValid: false,

      showConfirmation: false,
      paymentInProgress: false,
      dots: ".",
      payDisabled: false,
      paid: false,
      canMakePayment: null,

      cardElement: null,
      cardReady: false,

      redirect: false,
    };

    //REMOVE THE LINE BELOW THIS LATER
    this.props.setTiers(<FTCPathfinders13497Tiers/>);

    this.onSubmit = this.onSubmit.bind(this);
    this.onErrorSubmit = this.onErrorSubmit.bind(this);
    this.updateAmount = this.updateAmount.bind(this);
    this.updateRadioChoice = this.updateRadioChoice.bind(this);
    this.pay = this.pay.bind(this);
    this.updateDots = this.updateDots.bind(this);
    this.getButton = this.getButton.bind(this);
    this.genPaymentRequest = this.genPaymentRequest.bind(this);
  }

  onSubmit(event, formData) {
    event.preventDefault();
    event.stopPropagation();
    this.setState({amountValid: true, formValid: this.state.teamValid && this.state.paymentTypeValid});
  }

  onErrorSubmit(event, formData) {
    event.preventDefault();
    event.stopPropagation();
    this.setState({amountValid: false, formValid: false});
  }

  updateAmount(event) {
    let amount = event.target.value.replace(/[^0-9.]+/g, '');
    let decimalValue = parseFloat(amount);
    let valid = amountRegex.test(amount) && decimalValue >= 0.5 && decimalValue <= 999999.99;
    let radioChoice = this.state.radioAmount;
    if (amount !== "50" &&
        amount !== "50.0" &&
        amount !== "50.00" &&
        amount !== "100" &&
        amount !== "100.0" &&
        amount !== "100.00" &&
        amount !== "250" &&
        amount !== "250.0" &&
        amount !== "250.00" &&
        amount !== "500" &&
        amount !== "500.0" &&
        amount !== "500.00" &&
        amount !== "1000" &&
        amount !== "1000.0" &&
        amount !== "1000.00" &&
        amount !== "5000" &&
        amount !== "5000.0" &&
        amount !== "5000.00" &&
        amount !== ""
        ) {
          radioChoice = customAmount;
        }
    this.setState({amount: amount, radioAmount: radioChoice, amountValid: valid, formValid: valid ? this.state.teamValid && this.state.paymentTypeValid : false});
  }

  updateRadioChoice(event) {
    let amount = this.state.amount.replace(/[^0-9.]+/g, '');
    let decimalValue = parseFloat(amount);
    let valid = amountRegex.test(amount) && decimalValue >= 0.5 && decimalValue <= 999999.99;
    this.setState({amount: event.target.value !== customAmount ? event.target.value : this.state.amount, radioAmount: event.target.value, amountValid: valid, formValid: valid && this.state.teamValid && this.state.paymentTypeValid}, () => this.updateAmount({target: {value: this.state.radioAmount}}));
  }

  pay(event) {
    event.preventDefault();
    event.stopPropagation();
    if (this.state.formValid) {
      let clientInformation = this.props.getData();
      let data = {
        amount: parseFloat(this.state.amount * 100),
        description: `Gracious donation of ${this.state.amount} by ${this.state.paymentType} to ${this.state.team}.`,
        name: clientInformation.name,
        addr1: clientInformation.addr1,
        addr2: clientInformation.addr2,
        city: clientInformation.city,
        state: clientInformation.state,
        zip: clientInformation.zip,
        email: clientInformation.email,
        phone: clientInformation.phone,
      };
      getSecret(data).then((clientSecret) => {
        if (clientSecret !== null && clientSecret !== undefined && this.state.cardReady) {
          this.props.stripe.handleCardPayment(clientSecret, this.state.cardElement, {
            payment_method_data: {
              billing_details: {
                address: {
                  city: clientInformation.city,
                  line1: clientInformation.addr1,
                  line2: clientInformation.addr2,
                  postal_code: clientInformation.zip,
                  state: clientInformation.state,
                },
                email: clientInformation.email,
                name: clientInformation.name,
                phone: clientInformation.phone,
              },
            },
            receipt_email: clientInformation.email,
          }).then((result) => {
            if (result.error) {
              this.setState({showConfirmation: false, payDisabled: false, paymentInProgress: false, paid: false, error: "There's an issue with your payment. Please ensure your information is correct, try another payment method, or refresh the page and try again."});
            } else {
              paymentEmail(data);
              this.setState({paymentInProgress: false, paid: true}, () => window.setTimeout(() => this.setState({redirect: true}), 2500));
            }
          });
        } else {
          this.setState({showConfirmation: false, payDisabled: false, paymentInProgress: false, paid: false, error: "There's an issue with your payment. Please ensure your information is correct, try another payment method, or refresh the page and try again."});
        }
      });
    }
  }

  async updateDots() {
    if (this.state.paymentInProgress) {
      switch(this.state.dots.length) {
        case 1 : this.setState({dots: ".."}); break;
        case 2 : this.setState({dots: "..."}); break;
        default: this.setState({dots: "."}); break;
      }
      window.setTimeout(this.updateDots, updateInterval);
    }
  }

  genPaymentRequest() {
    let pr = this.props.stripe.paymentRequest({
      country: 'US',
      currency: 'usd',
      total: {
        label: "Donation to " + this.state.team,
        amount: parseFloat(this.state.amount) * 100,
      },
    });
    pr.on("token", ({complete, token}) => {
      let clientInformation = this.props.getData();
      let clientData = {
        amount: parseFloat(this.state.amount * 100),
        description: `Gracious donation of ${this.state.amount} by ${this.state.paymentType} to ${this.state.team}.`,
        name: clientInformation.name,
        addr1: clientInformation.addr1,
        addr2: clientInformation.addr2,
        city: clientInformation.city,
        state: clientInformation.state,
        zip: clientInformation.zip,
        email: clientInformation.email,
        phone: clientInformation.phone,
      };
      makePaymentRequest(token, clientData).then((value) => {
        if (value !== true) {
          complete('Payment unsuccessful. Please refresh the page, try another method, or try again later.');
        } else {
          complete('Thank you for your donation!');
        }
      });
    });
    pr.canMakePayment().then((result) => {
      this.setState({canMakePayment: !!result});
    });
    return pr;
  }

  getButton() {
    let comp;
    if (this.state.paymentType === creditDebitType) {
      let sub;
      if (this.state.paymentInProgress) {
        sub =
        <div>
          <Spinner
            as="span"
            animation="grow"
            size="sm"
            role="status"
            aria-hidden="true"
          />
          &nbsp;
          Donation in progress{this.state.dots}
        </div>;
      } else {
        sub = "Make Donation"
      }
      comp =
        <Button
          onClick={(event) => {this.setState({paymentInProgress: true, payDisabled: true}, () => window.setTimeout(this.updateDots, updateInterval)); this.pay(event);}}
          variant="outline-success"
          disabled={this.state.payDisabled}
        >
          {sub}
        </Button>;
    } else {
      if (this.state.canMakePayment === null) {
        comp = <Spinner animation="border" variant="primary" role="status"/>;
      } else {
        comp = this.state.canMakePayment ?
          <PaymentRequestButtonElement
            paymentRequest={this.state.paymentRequest}
            className="PaymentRequestButton paymentRequestButtonWidth"
            style={{paymentRequestButton: {theme: 'dark', type: 'donate', height: '38px'}}}
          /> :
          <p className="redText verticallyCenterThis alignThisRight">This payment method is unavailable.</p>
      }
    }
    return comp;
  }

  render() {
    return (
      <div>
        {this.state.redirect ? <Redirect push to="/"/> : null}
        <ValidationForm onSubmit={this.onSubmit} onErrorSubmit={this.onErrorSubmit} immediate={false}>
          <Form.Row>
            <div className="form-group">
              <label htmlFor="amount">Choose Your Amount (USD)</label> 
              <Radio.RadioGroup name="amount" required errorMsg="Please enter a valid amount." valueSelected={this.state.radioAmount} onChange={this.updateRadioChoice} inline={false}>
                <Radio.RadioItem id="50" label="$50" value="50"/>
                <Radio.RadioItem id="100" label="$100" value="100"/>
                <Radio.RadioItem id="250" label="$250" value="250"/>
                <Radio.RadioItem id="500" label="$500" value="500"/>
                <Radio.RadioItem id="1000" label="$1000" value="1000"/>
                <Radio.RadioItem id="5000" label="$5000" value="5000"/>
                <Radio.RadioItem id="custom" label="Custom" value={customAmount}/>
              </Radio.RadioGroup>
              <TextInputGroup
                required={this.state.radioAmount === customAmount}
                validator={this.state.radioAmount === customAmount ? () => true : (value) => amountRegex.test(value) && parseFloat(value) >= 0.5 && parseFloat(value) <= 999999.99}
                id="amount"
                name="amount"
                type="text"
                placeholder="12.34"
                prepend={<span className="input-group-text">$</span>}
                errorMessage={prepErrorMsgObj("amount")}
                value={this.state.amount}
                onChange={this.updateAmount}
              />
            </div>
          </Form.Row>
        </ValidationForm>
        <ButtonGroup className="mr-2">
        {/*
        <DropdownButton id="team" title={this.state.team}>
            <Dropdown.Item
              onSelect={() =>
                this.setState(
                  {
                    team: ftc13497,
                    teamValid: true,
                    formValid: this.state.amountValid && this.state.paymentTypeValid,
                    donateInfo: <Info13497/>
                  }, this.props.setTiers(<FTCPathfinders13497Tiers/>)
                )
              }
            >
              FTC Pathfinders 13497
            </Dropdown.Item>
            <Dropdown.Item
              onSelect={() =>
                this.setState(
                  {
                    team: fll7885,
                    teamValid: true,
                    formValid: this.state.amountValid && this.state.paymentTypeValid,
                    donateInfo: <Info7885/>
                  }, this.props.setTiers(<FLLPathfinders7885Tiers/>))
              }
            >
              FLL Phoenix Voyagers 7885
            </Dropdown.Item>
          </DropdownButton>
          &nbsp;
          */}
          <DropdownButton id="paymentType" title={this.state.paymentType}>
            <Dropdown.Item
              onSelect={() =>
                this.setState(
                  {
                    paymentType: creditDebitType,
                    paymentTypeValid: true,
                    formValid: this.state.amountValid && this.state.teamValid
                  })
              }
            >
              Credit / Debit
            </Dropdown.Item>
            <Dropdown.Item
              onSelect={() => 
                this.setState(
                  {
                    paymentType: paymentRequestType,
                    paymentTypeValid: true,
                    formValid: this.state.amountValid && this.state.teamValid
                  })
              }
            >
              Apple / Google Pay
            </Dropdown.Item>
          </DropdownButton>
        </ButtonGroup>
        <br/>
        <br/>
        {this.state.formValid && this.state.paymentType === creditDebitType ? <CardElement onReady={(element) => this.setState({cardElement: element, cardReady: true})}/>: null}
        <p className="redText">***We do not accept American Express cards. We apologize for any inconvenience.***</p>
        {this.state.error ? <p className="redText">{this.state.error}</p> : null}
        <br/>
        {this.state.donateInfo}
        <Button type="submit" disabled={!this.state.formValid} onClick={this.state.paymentType !== paymentRequestType ? () => this.setState({showConfirmation: true}) : () => this.setState({showConfirmation: true, paymentRequest: this.genPaymentRequest()})}>Make donation</Button>
        <Modal
          show={this.state.showConfirmation}
          aria-labelledby="contained-modal-title-vcenter"
          centered
          scrollable
          keyboard={false}
          backdrop="static"
        >
          <Modal.Header>
            <Modal.Title id="contained-modal-title-vcenter">
              <AsyncImage
                src={PathfindersRoboticsLogo}
                width="30"
                height="30"
                className="d-inline-block"
                alt="Pathfinders Robotics Logo"
                text="Payment Confirmation"
                helvetica="true"
              />
            </Modal.Title>
          </Modal.Header>
          <Modal.Body>
            {!this.state.paid ?
              <div>
                <h4>Please confirm your gracious donation of ${parseFloat(this.state.amount).toLocaleString()} by {this.state.paymentType.toLowerCase() === creditDebitType ? this.state.paymentType.toLocaleLowerCase() : this.state.paymentType} to the {this.state.team}</h4>
                <p>Pathfinders Robotics thanks you for your donation.</p>
              </div>
              :
              <div>
                <AnimatedCheckmark/>
                <p>This donation is tax deductible, and you can claim this deduction via our nonprofit, Pathfinders Robotics, with EIN "83-3047012".</p>
              </div>
            }
          </Modal.Body>
          <Modal.Footer>
            <Button onClick={() => this.setState({showConfirmation: false, payDisabled: false})} variant="outline-danger" disabled={this.state.paymentInProgress}>Cancel</Button>
            {this.getButton()}
          </Modal.Footer>
        </Modal>
      </div>
    );
  }
}

export default injectStripe(PaymentForm);
