import React, {Component} from 'react';
import AsyncImage from '../../widgets/AsyncImg';
import PathfindersRoboticsLogo from '../../../assets/Pathfinders-Logo.svg';
import queryString from 'querystring';
import '../../stylesheet.css';

const error = "Error ";

export default class Error extends Component {
    constructor(props) {
        super(props);
        this.getTitleFromCode = this.getTitleFromCode.bind(this);
        this.getDescriptionFromCode = this.getDescriptionFromCode.bind(this);
        this.getTitle = this.getTitle.bind(this);
        this.getDescription = this.getDescription.bind(this);
    }

    getTitleFromCode(code) {
        switch (code) {
            case 400:
                return " Bad Request";
            case 401:
                return " Unauthorized";
            case 402:
                return " Payment Required";
            case 403:
                return " Forbidden";
            case 404:
                return " Not Found";
            case 405:
                return " Method Not Allowed";
            case 406:
                return " Not Acceptable";
            case 407:
                return " Proxy Authentication Required";
            case 408:
                return " Request Timeout";
            case 409:
                return " Conflict";
            case 410:
                return " Gone";
            case 411:
                return " Length Required";
            case 412:
                return " Precondition Failed";
            case 413:
                return " Request Entity Too Large";
            case 414:
                return " Request-URI Too Long";
            case 415:
                return " Unsupported Media Type";
            case 416:
                return " Requested Range Not Satisfiable";
            case 417:
                return " Expectation Failed";
            case 418:
                return " I'm a teapot (RFC 2324)";
            case 420:
                return " Enhance Your Calm (Twitter)";
            case 422:
                return " Unprocessable Entity (WebDAV)";
            case 423:
                return " Locked (WebDAV)";
            case 424:
                return " Failed Dependency (WebDAV)";
            case 425:
                return " Reserved for WebDAV";
            case 426:
                return " Upgrade Required";
            case 428:
                return " Precondition Required";
            case 429:
                return " Too Many Requests";
            case 431:
                return " Request Header Fields Too Large";
            case 444:
                return " No Response (Nginx)";
            case 449:
                return " Retry With (Microsoft)";
            case 450:
                return " Blocked by Windows Parental Controls (Microsoft)";
            case 451:
                return " Unavailable For Legal Reasons";
            case 499:
                return " Client Closed Request (Nginx)";
            case 500:
                return " Internal Server Error";
            case 501:
                return " Not Implemented";
            case 502:
                return " Bad Gateway";
            case 503:
                return " Service Unavailable";
            case 504:
                return " Gateway Timeout";
            case 505:
                return " HTTP Version Not Supported";
            case 506:
                return " Variant Also Negotiates (Experimental)";
            case 507:
                return " Insufficient Storage (WebDAV)";
            case 508:
                return " Loop Detected (WebDAV)";
            case 509:
                return " Bandwidth Limit Exceeded (Apache)";
            case 510:
                return " Not Extended";
            case 511:
                return " Network Authentication Required";
            case 598:
                return " Network read timeout error";
            case 599:
                return " Network connect timeout error";
            default:
                return " Not Found";
        }
    }

    getDescriptionFromCode(code) {
        switch (code) {
            case 400:
                return " That's the wrong path to go down!";
            case 401:
                return " You're not allowed to go down that path!";
            case 402:
                return " You must pay to go down this path!";
            case 403:
                return " You're not allowed to go down that path!";
            case 404:
                return " We couldn't find that path!";
            case 405:
                return " You're not allowed to go down that path!";
            case 406:
                return " That path is unacceptable!";
            case 407:
                return " That path requires further security!";
            case 408:
                return " We've spent too long on that path!";
            case 409:
                return " That path is broken!";
            case 410:
                return " That path doesn't exist!";
            case 411:
                return " That path isn't long enough!";
            case 412:
                return " We couldn't start that path!";
            case 413:
                return " That path is too big!";
            case 414:
                return " That path is too long!";
            case 415:
                return " We don't take that kind of path!";
            case 416:
                return " That path doesn't work for us!";
            case 417:
                return " That path didn't meet expectations!";
            case 418:
                return " ... short and stout! / Here is my handle, here is my spout!";
            case 420:
                return " Blaze it, bro!";
            case 422:
                return " We couldn't process that path!";
            case 423:
                return " That path is locked!";
            case 424:
                return " That path doesn't have all it needs!";
            case 425:
                return " That path is reserved for someone else!";
            case 426:
                return " You need to improve to travel on that path!";
            case 428:
                return " You need to meet the requirements to travel on that path!";
            case 429:
                return " You've tried that path too many times!";
            case 431:
                return " Your shoes are too big for that path!";
            case 444:
                return " That path just doesn't support you!";
            case 449:
                return " When all other paths fail, try, try, try again!";
            case 450:
                return " That path has been blocked!";
            case 451:
                return " That path is illegal!";
            case 499:
                return " You closed access to that path!";
            case 500:
                return " There's an issue with the destination!";
            case 501:
                return " This path isn't correctly paved!";
            case 502:
                return " This path is inaccessible!";
            case 503:
                return " This destination is unavailable!";
            case 504:
                return " This path took too long to pave!";
            case 505:
                return " This path doesn't support your method of travel!";
            case 506:
                return " This path is sassy!";
            case 507:
                return " There's not enough storage for this path!";
            case 508:
                return " This path results in a loop!";
            case 509:
                return " This path requires too much bandwidth!";
            case 510:
                return " This path has not been extended!";
            case 511:
                return " This path requires border patrol!";
            case 598:
                return " Border patrol took too long on this path!";
            case 599:
                return " Border patrol took too long on this path!";
            default:
                return " We couldn't find that path!";
        }
    }

    getTitle() {
        if (!!this.props.notFound) {
            return error + "404: " + this.getTitleFromCode(404);
        }
        if (this.props.location.search) {
            let vals = queryString.parse(this.props.location.search.substr(1));
            if (vals) {
                if (vals.code) {
                    return error + vals.code.toString() + ': '+ this.getTitleFromCode(parseInt(vals.code));
                }
            }
        }
        return error + this.getTitleFromCode(404);
    }

    getDescription() {
        if (!!this.props.notFound) {
            return this.getDescriptionFromCode(404);
        }
        if (this.props.location.search) {
            let vals = queryString.parse(this.props.location.search.substr(1));
            if (vals) {
                if (vals.code) {
                    return this.getDescriptionFromCode(parseInt(vals.code));
                }
            }
        }
        return this.getDescriptionFromCode(404);
    }

    render() {
        return (
            <div className="centeredDiv">
                <h1 className="centeredTitle">{this.getTitle()}</h1>
                <h4 className="centeredTitle">{this.getDescription()}</h4>
                <AsyncImage src={PathfindersRoboticsLogo} alt="Pathfinders Robotics Logo" responsive="true" />
            </div>
        );
    }
}
