// @flow

import React, { Component } from 'react';
import { Container, Row, Col, ListGroup, Nav, Button, NavDropdown} from 'react-bootstrap';
import ScrollableAnchor from 'react-scrollable-anchor';
import { configureAnchors } from 'react-scrollable-anchor';
import {Helmet} from 'react-helmet';
import AsyncImage from '../../../widgets/AsyncImg';
import AndrewScott from '../../../../assets/teams/7885/AndrewScott.jpg';
import CarsonBaty from '../../../../assets/teams/7885/CarsonBaty.jpg';
import ErinStone from '../../../../assets/teams/7885/ErinStone.jpg';
import GabriellaAvilez from '../../../../assets/teams/7885/GabriellaAvilez.jpg';
import MasonHayes from '../../../../assets/teams/7885/MasonHayes.jpg';
import SamyuMogolapalli from '../../../../assets/teams/7885/SamyuMogolapalli.jpg';
import TeamCompressed from '../../../../assets/teams/7885/Team7885.jpg';
import ReactGA from 'react-ga';

import '../../../stylesheet.css';

const nav =
<Nav variant="pills" className="flex-column" defaultActiveKey="#FLLPathfinders7885">
  <Nav.Item><Nav.Link href="#FLLPathfinders7885">FLL Phoenix Voyagers 7885</Nav.Link></Nav.Item>
  <Nav.Item><Nav.Link href="#FLLPathfinders7885Mission">Mission</Nav.Link></Nav.Item>
  <Nav.Item><Nav.Link href="#FLLPathfinders7885Finances">Finances</Nav.Link></Nav.Item>
  <Nav.Item><Nav.Link href="#FLLPathfinders7885Team">Meet the Team</Nav.Link></Nav.Item>
</Nav>
;

const minWidth = 775;

export default class SEVEN_EIGHT_EIGHT_FIVE extends Component {

  constructor(props) {
    super(props);
    this.state = {
      width: window.innerWidth,
      selected: "FLL Phoenix Voyagers 7885"
    }
    this.updateWindowWidth = this.updateWindowWidth.bind(this);
  }

  componentWillMount() {
    configureAnchors({offset: -90});
    window.addEventListener('resize', this.updateWindowWidth);
  }

  componentDidMount() {
    ReactGA.pageview(window.location.pathname);
    this.updateWindowWidth();
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.updateWindowWidth);
  }

  updateWindowWidth() {
    this.setState({width: window.innerWidth});
    if (this.state.width < minWidth) {
      configureAnchors({offset: -190});
    } else {
      configureAnchors({offset: -90});
    }
  }

  render() {
    return (
      <div id="7885">
        <Helmet>
          <title>FLL Phoenix Voyagers 7885 | Pathfinders Robotics</title>
          <meta name="description" content="FLL Pathfinders 7885 started as a feeder team to the FTC Pathfinders 13497. We're composed of six students between fourth and seventh grade. Many of us ..." />
        </Helmet>
        <Container fluid>
          <Row>
            <Col className="teamArticle standardFont">
              <ScrollableAnchor id="FLLPathfinders7885">
                <div>
                  {this.state.width < minWidth ?
                    <div>
                      <div className="mobileBar">
                        <Nav variant="pills" className="flex-column frozen">
                          <NavDropdown title={this.state.selected}>
                            <NavDropdown.Item href="#FLLPathfinders7885" onClick={() => this.setState({selected: "FLL Phoenix Voyagers 7885"})}>FLL Phoenix Voyagers 7885</NavDropdown.Item>
                            <NavDropdown.Item href="#FLLPathfinders7885Mission" onClick={() => this.setState({selected: "Mission"})}>Mission</NavDropdown.Item>
                            <NavDropdown.Item href="#FLLPathfinders7885Finances" onClick={() => this.setState({selected: "Finances"})}>Finances</NavDropdown.Item>
                            <NavDropdown.Item href="#FLLPathfinders7885Team" onClick={() => this.setState({selected: "Meet the Team"})}>Meet the Team</NavDropdown.Item>
                          </NavDropdown>
                        </Nav>
                      </div>
                      <br/>
                      <br/>
                    </div> : null
                  }
                  <h1>FLL Phoenix Voyagers 7885</h1>
                  <AsyncImage src={TeamCompressed} alt="FLL Phoenix Voyagers 7885" responsive="true"/>
                  <p>Left to right: Samu Mogallapalli, Gabriella Avilez, Mason Hayes, Carson Baty, Andrew Scott, Erin Stone</p>
                  <p>
                    FLL Phoenix Voyagers 7885 started as a feeder team to the FTC
                    Pathfinders 13497. We're composed of six students between
                    fourth and seventh grade. Many of us are the younger siblings
                    of the FTC Pathfinders 13497 team.
                  </p>
                  <p>
                    We advanced to the FLL State Championship right from our very
                    first competition this past December. At the FLL State
                    Championship, we received third place for the Project Award.
                    In our future, we hope to advance to the national competition
                    and inspire others to enjoy STEM as much as we do.
                  </p>
                </div>
              </ScrollableAnchor>
              <ScrollableAnchor id="FLLPathfinders7885Mission">
                <div>
                  <h1>Our Mission</h1>
                  <p>
                    Our mission is to understand and promote STEM as best we can by
                    building and programming a robot and completing a research project
                    through the use of teamwork and FLL's Core Values.
                  </p>
                </div>
              </ScrollableAnchor>
              <ScrollableAnchor id="FLLPathfinders7885Finances">
                <div>
                  <h1>Finances</h1>
                  <h2>How We Spend Our Money</h2>
                  <p>
                    Each season, we must pay for...
                  </p>
                  <ListGroup>
                      <ListGroup.Item>Robot Game Mat</ListGroup.Item>
                      <ListGroup.Item>Robot Game Elements</ListGroup.Item>
                      <ListGroup.Item>Project Components</ListGroup.Item>
                      <ListGroup.Item>FLL Registration</ListGroup.Item>
                      <ListGroup.Item>Competition Entrance Fees</ListGroup.Item>
                      <ListGroup.Item>Team T-shirts</ListGroup.Item>
                    </ListGroup>
                  <h2>How Donations Make A Difference</h2>
                  <p>
                    Donations allow us to compete in more competitions and upgrade
                    robot parts and equipment. Donations also allow us to reach out
                    to the community to make connections and work with experts.
                    Finally, we use donations to promote STEM alongside the FTC
                    Pathfinders 13497. None of this would be possible without the
                    many donations from our gracious sponsors.
                  </p>
                </div>
              </ScrollableAnchor>
              <ScrollableAnchor id="FLLPathfinders7885Team">
                <div>
                  <h1>Meet The Team</h1>
                  <br/>
                  <AsyncImage src={GabriellaAvilez} alt="Gabriella Avilez"/>
                  <h3>Gabriella Avilez</h3>
                  <br/>
                  <AsyncImage src={CarsonBaty} alt="Carson Baty"/>
                  <h3>Carson Baty, Builder, Programmer</h3>
                  <br/>
                  <AsyncImage src={MasonHayes} alt="Mason Hayes"/>
                  <h3>Mason Hayes</h3>
                  <br/>
                  <AsyncImage src={SamyuMogolapalli} alt="Samu Mogallapalli"/>
                  <h3>Samu Mogallapalli, Builder, Programmer</h3>
                  <br/>
                  <AsyncImage src={AndrewScott} alt="Andrew Scott"/>
                  <h3>Andrew Scott, Builder, Programmer</h3>
                  <br/>
                  <AsyncImage src={ErinStone} alt="Erin Stone"/>
                  <h3>Erin Stone, Project Captain</h3>
                  <br/>
                </div>
              </ScrollableAnchor>
            </Col>
            <Col md="auto" className="sidenav">
              {this.state.width > minWidth ? nav : null}
              <Button variant="primary" href="#FLLPathfinders7885" className="backToTop">Back to top</Button>
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}
