import React, {Component} from 'react';
import {ValidationForm, TextInput, Checkbox} from 'react-bootstrap4-form-validation';
import {conformToMask} from 'react-text-mask';
import { Button, Form } from 'react-bootstrap';

const shortStateRegex = /^(?:A[KLRSZ]|C[AOT]|D[CE]|FL|G[AU]|HI|I[ADLN]|K[SY]|LA|M[ADEINOPST]|N[CDEHJMVY]|O[HKR]|P[AR]|RI|S[CD]|T[NX]|UT|V[AIT]|W[AIVY])*$/i;
const longStateRegex = /^(?:ALABAMA|ALASKA|ARIZONA|ARKANSAS|CALIFORNIA|COLORADO|CONNECTICUT|DELAWARE|FLORIDA|GEORGIA|HAWAII|IDAHO|ILLINOIS|INDIANA|IOWA|KANSAS|KENTUCKY|LOUISIANA|MAINE|MARYLAND|MASSACHUSETTS|MICHIGAN|MINNESOTA|MISSISSIPPI|MISSOURI|MONTANA|NEBRASKA|NEVADA|NEW\sHAMPSHIRE|NEW\sJERSEY|NEW\sMEXICO|NEW\sYORK|NORTH\sCAROLINA|NORTH\sDAKOTA|OHIO|OKLAHOMA|OREGON|PENNSYLVANIA|RHODE\sISLAND|SOUTH\sCAROLINA|SOUTH\sDAKOTA|TENNESSEE|TEXAS|UTAH|VERMONT|VIRGINIA|WASHINGTON|WEST\sVIRGINIA|WASHINGTON\sDC|WASHINGTON\sD\.C\.|DISTRICT\sOF\sCOLUMBIA|AMERICAN\sSAMOA|SAMOA|GUAM|NORTHERN\sMARIANA\sISLANDS|NORTHERN\sMARIANA|MARIANA\sISLANDS|MARIANA|PUERTO\sRICO|VIRGIN\sISLANDS|VIRGIN)/i;
const zipRegex = /^([0-9]{5}(?:-[0-9]{4})?)*$/;
const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/i;
const phoneRegex = /(\+1\s\([0-9]{3}\)\s[0-9]{3}-[0-9]{4})/;

const phoneMask = ['+', '1', ' ', '(', /[1-9]/, /\d/, /\d/, ')', ' ', /\d/, /\d/, /\d/, '-', /\d/, /\d/, /\d/, /\d/];

const defaultErrorMsgStr = "Please enter a valid ";
const period = ".";

export function prepErrorMsgObj(errorMsgTarget) {
    if (errorMsgTarget != null) {
        let properStr = defaultErrorMsgStr + errorMsgTarget + period;
        return {
            required: properStr,
            pattern: properStr,
            type: properStr,
            step: properStr,
            minLength: properStr,
            min: properStr,
            max: properStr,
            fileType: properStr,
            maxFileSize: properStr,
            validator: properStr,
        };
    }
    return null;
}

const nameErrorMsg = prepErrorMsgObj("name");
const addrErrorMsg = prepErrorMsgObj("address");
const cityErrorMsg = prepErrorMsgObj("city");
const stateErrorMsg = prepErrorMsgObj("state");
const zipErrorMsg = prepErrorMsgObj("zip / postal code");
const emailErrorMsg = prepErrorMsgObj("email");
const phoneErrorMsg = prepErrorMsgObj("phone number");

export default class InformationForm extends Component {
    constructor(props) {
        super(props);
        this.state = {
            name: '',
            addr1: '',
            addr2: '',
            city: '',
            state: '',
            zip: '',
            email: '',
            phone: '',
            contactAllowed: true,
            formValid: false,
        }
        this.onSubmit = this.onSubmit.bind(this);
        this.onErrorSubmit = this.onErrorSubmit.bind(this);
        this.updateTabs = this.updateTabs.bind(this);
        this.updateVal = this.updateVal.bind(this);
        this.updateState = this.updateState.bind(this);
        this.updatePhone = this.updatePhone.bind(this);
        this.twoLetterStateFromFullState = this.twoLetterStateFromFullState.bind(this);
    }

    // Submit event targeting the form
    // Serialized formData object. Keys are input names.
    onSubmit(event, formData) {
        event.preventDefault();
        event.stopPropagation();
        this.setState({formValid: true}, this.updateTabs);
    }

    onErrorSubmit(event, formData) {
        event.preventDefault();
        event.stopPropagation();
        this.setState({formValid: false}, this.updateTabs);
    }

    updateTabs() {
        if (this.state.formValid) {
            this.props.enablePayment(() => this.props.setData({
                name: this.state.name,
                addr1: this.state.addr1,
                addr2: this.state.addr2,
                city: this.state.city,
                state: this.state.state.length > 2 ? this.twoLetterStateFromFullState(this.state.state) : this.state.state,
                zip: this.state.zip,
                email: this.state.email,
                phone: this.state.phone,
                amount: this.props.getData().amount
            }, this.props.advanceToFront));
        } else {
            this.props.disablePayment();
        }
    }

    updateVal(event) {
        this.setState({[event.target.name]: event.target.value});
    }

    updateState(event) {
        this.setState({state: event.target.value.replace(/[^a-zA-Z.]+/g, '')});
    }

    updatePhone(event) {
        this.setState({phone: conformToMask(event.target.value, phoneMask, {guide: false}).conformedValue});
    }

    render() {
        return (
            <ValidationForm onSubmit={this.onSubmit} onErrorSubmit={this.onErrorSubmit} immediate={false}>
                <Form.Row>
                    <div className="form-group">
                        <label htmlFor="name">Name</label>
                        <TextInput
                            required
                            id="name"
                            name="name"
                            type="text"
                            placeholder="John Doe"
                            errorMessage={nameErrorMsg}
                            value={this.state.name}
                            onChange={this.updateVal}
                        />
                    </div>
                </Form.Row>
                <Form.Row>
                    <div className="form-group">
                        <label htmlFor="addr1">Address Line 1</label>
                        <TextInput
                            required
                            id="addr1"
                            name="addr1"
                            type="text"
                            placeholder="12345 Artificial Dr"
                            errorMessage={addrErrorMsg}
                            value={this.state.addr1}
                            onChange={this.updateVal}
                        />
                    </div>
                    &nbsp;
                    <div className="form-group">
                        <label htmlFor="addr2">Address Line 2</label>
                        <TextInput
                            id="addr2"
                            name="addr2"
                            type="text"
                            placeholder="Apt. 27"
                            errorMessage={addrErrorMsg}
                            value={this.state.addr2}
                            onChange={this.updateVal}
                        />
                    </div>
                </Form.Row>
                <Form.Row>
                    <div className="form-group">
                        <label htmlFor="city">City</label>
                        <TextInput
                            required
                            id="city"
                            name="city"
                            type="text"
                            placeholder="City"
                            errorMessage={cityErrorMsg}
                            value={this.state.city}
                            onChange={this.updateVal}
                        />
                    </div>
                    &nbsp;
                    <div className="form-group">
                        <label htmlFor="state">State</label>
                        <TextInput
                            required
                            validator={(value) => shortStateRegex.test(value) || longStateRegex.test(value)} 
                            id="state"
                            name="state"
                            type="text"
                            placeholder="State"
                            maxLength="25"
                            errorMessage={stateErrorMsg}
                            value={this.state.state}
                            onChange={this.updateState}
                        />
                    </div>
                    &nbsp;
                    <div className="form-group">
                        <label htmlFor="zip">Zip / Postal Code</label>
                        <TextInput
                            required
                            validator={(value) => zipRegex.test(value)}
                            id="zip"
                            name="zip"
                            type="text"
                            placeholder="12345"
                            maxLength="5"
                            errorMessage={zipErrorMsg}
                            value={this.state.zip}
                            onChange={(event) => this.setState({zip: !isNaN(event.target.value) ? event.target.value : this.state.zip})}
                        />
                    </div>
                </Form.Row>
                <Form.Row>
                    <div className="form-group">
                        <label htmlFor="email">Email</label>
                        <TextInput
                            required
                            validator={(value) => emailRegex.test(value)}
                            id="email"
                            name="email"
                            type="email"
                            placeholder="johndoe@domain.tld"
                            errorMessage={emailErrorMsg}
                            value={this.state.email}
                            onChange={this.updateVal}
                        />
                    </div>
                    &nbsp;
                    <div className="form-group">
                        <label htmlFor="phone">Phone Number</label>
                        <TextInput
                            required
                            validator={(value) => phoneRegex.test(value)}
                            id="phone"
                            name="phone"
                            type="tel"
                            placeholder="+1 (123) 456-7890"
                            errorMessage={phoneErrorMsg}
                            value={this.state.phone}
                            onChange={this.updatePhone}
                        />
                    </div>
                </Form.Row>
                <Form.Row>
                    <div className="form-group">
                        <Checkbox
                            id="contactAllowed"
                            name="contactAllowed"
                            type="checkbox"
                            value={this.state.contactAllowed}
                            onChange={(_, value) => {this.setState({contactAllowed: value})}}
                            label="Receive updates from donation recipients throughout the season"
                        />
                    </div>
                </Form.Row>
                <Form.Row>
                    <div>
                        <Button type="submit">Submit</Button>
                    </div>
                </Form.Row>
            </ValidationForm>
        );
    }

    twoLetterStateFromFullState(fullState) {
        switch (fullState.toUpperCase()) {
            case "ALABAMA"                 : return "AL";
            case "ALASKA"                  : return "AK";
            case "ARIZONA"                 : return "AZ";
            case "ARKANSAS"                : return "AK";
            case "CALIFORNIA"              : return "CA";
            case "COLORADO"                : return "CO";
            case "CONNECTICUT"             : return "CT";
            case "DELAWARE"                : return "DE";
            case "FLORIDA"                 : return "FL";
            case "GEORGIA"                 : return "GA";
            case "HAWAII"                  : return "HI";
            case "IDAHO"                   : return "ID";
            case "ILLINOIS"                : return "IL";
            case "INDIANA"                 : return "IN";
            case "IOWA"                    : return "IA";
            case "KANSAS"                  : return "KS";
            case "KENTUCKY"                : return "KY";
            case "LOUISIANA"               : return "LA";
            case "MAINE"                   : return "ME";
            case "MARYLAND"                : return "MD";
            case "MASSACHUSETTS"           : return "MA";
            case "MICHIGAN"                : return "MI";
            case "MINNESOTA"               : return "MN";
            case "MISSISSIPPI"             : return "MS";
            case "MISSOURI"                : return "MO";
            case "MONTANA"                 : return "MT";
            case "NEBRASKA"                : return "NE";
            case "NEVADA"                  : return "NV";
            case "NEW HAMPSHIRE"           : return "NH";
            case "NEW JERSEY"              : return "NJ";
            case "NEW MEXICO"              : return "NM";
            case "NEW YORK"                : return "NY";
            case "NORTH CAROLINA"          : return "NC";
            case "NORTH DAKOTA"            : return "ND";
            case "OHIO"                    : return "OH";
            case "OKLAHOMA"                : return "OK";
            case "OREGON"                  : return "OR";
            case "PENNSYLVANIA"            : return "PA";
            case "RHODE ISLAND"            : return "RI";
            case "SOUTH CAROLINA"          : return "SC";
            case "SOUTH DAKOTA"            : return "SD";
            case "TENNESSEE"               : return "TN";
            case "TEXAS"                   : return "TX";
            case "UTAH"                    : return "UT";
            case "VERMONT"                 : return "VT";
            case "VIRGINIA"                : return "VA";
            case "WASHINGTON"              : return "WA";
            case "WEST VIRGINIA"           : return "WV";
            case "WISCONSIN"               : return "WI";
            case "WYOMING"                 : return "WY";
            case "WASHINGTON DC"           : return "DC";
            case "WASHINGTON D.C."         : return "DC";
            case "DISTRICT OF COLUMBIA"    : return "DC";
            case "AMERICAN SAMOA"          : return "AS";
            case "SAMOA"                   : return "AS";
            case "GUAM"                    : return "GU";
            case "NORTHERN MARIANA ISLANDS": return "MP";
            case "NORTHERN MARIANA"        : return "MP";
            case "MARIANA ISLANDS"         : return "MP";
            case "MARIANA"                 : return "MP";
            case "PUERTO RICO"             : return "PR";
            case "VIRGIN ISLANDS"          : return "VI";
            case "VIRGIN"                  : return "VI";
            default                        : return null;
        }
    }
}