export default class Create extends Component {
  constructor(props) {
    super(props);
    this.state = {
      width: window.innerWidth,
      selected: "What Does Creating A Team Entail?",
    };
    this.updateWindowWidth = this.updateWindowWidth.bind(this);
  }

  componentWillMount() {
    configureAnchors({offset: -90});
    window.addEventListener('resize', this.updateWindowWidth);
  }

  componentDidMount() {
    ReactGA.pageview(window.location.pathname);
    this.updateWindowWidth();
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.updateWindowWidth);
  }

  updateWindowWidth() {
    this.setState({width: window.innerWidth});
    if (this.state.width < minWidth) {
      configureAnchors({offset: -190});
    } else {
      configureAnchors({offset: -90});
    }
  }

  render() {
    return (
      <div id="create" className="centerThis">
        <AsyncImage src={UnderConstruction} alt="Under Construction" responsive="true" />
      </div>
    );
  }
}
