export async function getSecret(data) {
    if (navigator.onLine) {
        let request = new XMLHttpRequest();
        request.open("POST", "/getSecret", false);
        request.setRequestHeader("Content-Type", "application/json");
        request.send(JSON.stringify(data));
        if (request.status === 200) {
            return JSON.parse(request.responseText).secret;
        }
    }
    return null;
}

// Server must return json object with boolean success property
export async function makePaymentRequest(token, data) {
    if (navigator.onLine) {
        let request = new XMLHttpRequest();
        request.open("POST", "/paymentRequest", false);
        request.setRequestHeader("Content-Type", "application/json");
        let finalObj = { };
        Object.assign(finalObj, data);
        Object.assign(finalObj, { token: token.id });
        request.send(JSON.stringify(finalObj));
        if (request.status === 200) {
            var response = JSON.parse(request.responseText);
                if (response !== null && response !== undefined) {
                    // Looks weird but is necessary because of type coercion
                    return response.success === true;
                } else {
                    return false;
                }
        }
    }
    return null; 
}

export async function paymentEmail(data) {
    let request = new XMLHttpRequest();
    request.open("POST", "/paymentEmail", true);
    request.setRequestHeader("Content-Type", "application/json");
    request.send(JSON.stringify(data));
}