// @flow

import React, { Component } from 'react';
import { injectStripe } from 'react-stripe-elements';
import { Tabs, Tab } from 'react-bootstrap';
import InformationForm from './InformationForm';
import PaymentForm from './PaymentForm';

const information = "information";
const payment = "payment";

class CheckoutForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      activeKey: information,
      paymentDisabled: true,
      publicData: {
        name: '',
        addr1: '',
        addr2: '',
        city: '',
        state: '',
        zip: '',
        email: '',
        phone: '',
        amount: '',
      }
    };

    this.onSelect = this.onSelect.bind(this);
    this.advanceToFront = this.advanceToFront.bind(this);
    this.disablePayment = this.disablePayment.bind(this);
    this.enablePayment = this.enablePayment.bind(this);
    this.getData = this.getData.bind(this);
    this.setData = this.setData.bind(this);
  }

  onSelect(key) {
    this.setState({ activeKey: key,
      paymentDisabled: this.state.paymentDisabled ? true : (this.state.activeKey === payment && key === information),
    });
  }

  advanceToFront() {
    if (!this.state.paymentDisabled) {
      this.onSelect(payment);
    } else {
      this.onSelect(information);
    }
  }

  render() {
    return (
      <Tabs activeKey={this.state.activeKey} onSelect={this.onSelect}>
        <Tab eventKey={information} title="Contact Information">
          <br />
          <InformationForm
            disablePayment={this.disablePayment}
            enablePayment={this.enablePayment}
            getData={this.getData}
            setData={this.setData}
            advanceToFront={this.advanceToFront}
          />
        </Tab>
        <Tab
          eventKey={payment}
          title="Payment"
          disabled={this.state.paymentDisabled}
        >
          <br />
          <PaymentForm
            getData={this.getData}
            setData={this.setData}
            advanceToFront={this.advanceToFront}
            setTiers={this.props.setTiers}
          />
        </Tab>
      </Tabs>
    );
  }

  disablePayment(callback) {
    if (typeof callback !== "function") {
      this.setState({ paymentDisabled: true });
    } else {
      this.setState({ paymentDisabled: true }, callback);
    }
  }

  enablePayment(callback) {
    if (typeof callback !== "function") {
      this.setState({ paymentDisabled: false});
    } else {
      this.setState({ paymentDisabled: false }, callback);
    }
  }

  getData() {return this.state.publicData};

  setData(data, callback) {
    if (typeof callback !== "function") {
      this.setState({ publicData: data });
    } else {
      this.setState({ publicData: data }, callback);
    }
  }
}

export default injectStripe(CheckoutForm);
